.footer {
    background: linear-gradient(135deg, #FF4B2B, #1D976C); 
    color: white; 
    padding: 40px 10%; 
    margin-top: 20px; 
    overflow: hidden;
}

.footer-bottom {
    display: flex; 
    align-items: center; 
    justify-content: space-between; 
    flex-wrap: wrap; 
}

.contact-section {
    display: flex; 
    align-items: center; 
    margin-bottom: 20px; 
}

.profile-image {
    width: 90px; 
    height: 90px; 
    border-radius: 50%; 
    object-fit: cover;
    margin-right: 20px; 
    border: 2px solid #fff; 
    transition: transform 0.3s ease; 
}

.profile-image:hover {
    transform: scale(1.1); 
}

.get-in-touch {
    text-align: left; 
}

.quote-section {
    text-align: center; 
    flex-grow: 1; 
    margin: 20px 0; 
}

.quote {
    font-size: 22px; 
    font-style: italic;
    color: #fff; 
    margin: 0; 
    transition: color 0.3s ease; 
}

.quote:hover {
    color: #FF4B2B; 
}

.social-media-icons {
    display: flex; 
    justify-content: center; 
    gap: 20px; 
    margin: 10px 0; 
    flex-wrap: wrap; 
}

.social-icon {
    display: flex; 
    flex-direction: column; 
    align-items: center; 
    justify-content: center;
}

.social-icon a {
    color: white; 
    font-size: 28px; 
    transition: transform 0.3s ease, color 0.3s ease; 
    text-decoration: none;
}

.social-icon span {
    font-size: 14px; 
    margin-top: 5px; 
    color: #f9f9f9; 
}

.social-icon a:hover {
    transform: translateY(-5px); 
    color: #FFD700; 
}

.footer-bottom-text {
    text-align: center; 
    margin-top: 20px; 
    font-size: 16px; 
}


/* Medium Screens */
@media (max-width: 768px) {
    .footer-bottom {
        flex-direction: column; 
        align-items: center; 
    }

    .contact-section {
        margin-bottom: 10px;
    }
}

/* Large Screens */
@media (min-width: 769px) {
    .footer-bottom {
        justify-content: center;
    }

    .social-media-icons {
        justify-content: center; 
    }
}
