:root {
    scroll-padding-top: 70px;
}

.image-container {
    position: relative;
    display: inline-block;
    width: 100%; /* Adjust according to your layout */
  }
  
  .protected-image {
    width: 100%; /* Ensures responsive design */
    pointer-events: none; /* Disable interaction with the image */
    user-select: none; /* Prevents text/image selection */
  }
  
  .image-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0); /* Invisible overlay */
    z-index: 1;
  }
  
.about {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 30px;
    margin: 10px 130px;
}


.about-title h1 {
    font-size: 600;
    color: #e63946;
    margin-bottom: 10px;
    position: relative;
    display: inline-block;
}

.about-title h1::after {
    content: "";
    width: 50%;
    height: 4px;
    background-color: #e63946;
    display: block;
    margin: 10px auto 0;
    border-radius: 2px;
}

.about-sections {
    display: flex;
    justify-content: space-between;
    width: 100%;
    gap: 60px;
    flex-wrap: wrap;
}

.about-left img {
    width: 300px;
    height: 400px;
    border-radius: 20px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s, box-shadow 0.3s;
}

.about-left img:hover {
    transform: scale(1.05);
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2);
}

.about-right {
    display: flex;
    flex-direction: column;
    gap: 40px;
    flex: 1;
}

.about-para {
    font-weight: 500;
    font-size: 18px;
    line-height: 1.6;
    color: #333;
}

.about-skills {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.about-skill {
    display: flex;
    align-items: center;
    transition: transform 0.3s;
}

.about-skill p {
    min-width: 150px;
    font-size: 20px;
    font-weight: 500;
}

.about-skill hr {
    outline: none;
    border: none;
    width: 100%;
    height: 8px;
    border-radius: 50px;
    background: linear-gradient(264deg, #DF8908 -5.09%, #B415FF 106.28%);
}

.about-achievements {
    display: flex;
    width: 100%;
    justify-content: space-between;
    margin-bottom: 60px;
}

.about-achievement {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    transition: transform 0.5s;
}

.about-achievement h1 {
    font-size: 50px;
    font-weight: 700;
    color: #e63946;
    text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.2);
}

.about-achievement p {
    font-size: 20px;
    font-weight: 500;
    color: #555;
}


.about-skill:hover {
    transform: scale(1.03);
}

.about-achievement:hover {
    transform: scale(1.05);
}


@media (max-width: 768px) {
    .about {
        margin: 40px 20px;
        gap: 30px;
    }

    .about-title h1 {
        font-size: 48px;
    }

    .about-sections {
        flex-direction: column;
        align-items: center;
    }

    .about-left img {
        width: 200px;
    }

    .about-para {
        font-size: 16px;
    }

    .about-skill p {
        font-size: 18px;
    }

    .about-achievement h1 {
        font-size: 40px;
    }

    .about-achievement p {
        font-size: 18px;
    }
}