:root {
  scroll-padding-top: 80px;
}

.skills-container {
  padding-top: 60px;
}

.skills-container {
  background-color: #ffffff;
  padding: 10px 20px;
  text-align: center;
  color: #1a1a1a;
  font-family: 'Poppins', sans-serif;
}

.skills-heading {
  font-size: 600;
  color: #e63946;
  margin-bottom: 30px;
  position: relative;
  display: inline-block;
}

.skills-heading::after {
  content: "";
  width: 50%;
  height: 4px;
  background-color: #e63946;
  display: block;
  margin: 10px auto 0;
  border-radius: 2px;
}

.skills-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.skills-card {
  background-color: #f5f5f5;
  margin: 10px;
  padding: 20px;
  max-width: 300px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
  animation: fadeIn 0.8s ease-in-out;
}

.skills-card:hover {
  transform: scale(1.05);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2);
}

.html-icon {
  color: #e34f26;
}

.css-icon {
  color: #2965f1;
}

.bootstrap-icon {
  color: #563d7c;
}

.github-icon {
  color: #000000;
}

.java-icon {
  color: #f89820;
}

.react-icon {
  color: #61dafb;
}

.node-icon {
  color: #68a063;
}

.mongodb-icon {
  color: #4db33d;
}

.aws-icon {
  color: #ff9900;
}

.icon-container {
  font-size: 3rem;
  color: #e63946;
  margin-right: 15px;
  animation: bounce 2s infinite;
}

.text-container {
  text-align: left;
}

.skill-name {
  font-size: 1.5rem;
  color: #e63946;
  margin: 5px 0;
}

.skill-description {
  font-size: 1rem;
  color: #6c757d;
  line-height: 1.5;
}

@media (max-width: 768px) {
  .skills-card {
    max-width: 100%;
    flex-direction: column;
    text-align: center;
  }

  .icon-container {
    margin: 0 0 10px 0;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes bounce {

  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }

  40% {
    transform: translateY(-10px);
  }

  60% {
    transform: translateY(-5px);
  }
}