.hero {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 35px;
}

.social-icons-left,
.social-icons-right {
    position: absolute;
    top: 30%;
    transform: translateY(-50%);
    z-index: 1;
    opacity: 0;
    animation: fadeIn 0.5s forwards;
}


.social-icons-left {
    left: 50px;
    animation-delay: 0.2s;
}

.social-icons-right {
    right: 50px;
    animation-delay: 0.4s;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}


.social-icons-left a,
.social-icons-right a {
    display: block;
    margin: 10px 0;
    color: #ff0000;
    font-size: 34px;
    transition: transform 0.3s, color 0.3s;
}


.social-icons-left a:hover,
.social-icons-right a:hover {
    color: #3bc301;
    transform: scale(1.2);
}


.hero img {
    margin-top: 100px;
    border-radius: 50%;
    height: 300px;
    width: 300px;
    object-fit: cover;
    box-shadow: 0px 20px 50px rgba(255, 255, 255, 0.5);
    transition: transform 0.5s ease, box-shadow 0.5s ease;
}

.hero img:hover {
    box-shadow: 0px 15px 40px rgba(136, 136, 136, 0.3);
    transform: scale(1.15);
}


.hero h1 {
    text-align: center;
    width: 70%;
    font-size: 80px;
    font-weight: 600;
    color: #1D976C;
    position: relative;
    z-index: 2;
}

.hero h1 span {
    background: linear-gradient(270deg, #ed7575 10%, #df0808 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.hero p {
    width: 50%;
    text-align: center;
    font-size: 24px;
    line-height: 30px;
    z-index: 2;
}

.hero-action {
    display: flex;
    align-items: center;
    gap: 30px;
    font-size: 24px;
    font-weight: 500;
    margin-bottom: 30px;
}

.hero-connect,
.hero-resume {
    padding: 10px 20px;
    border: 2px solid #4f46e5;
    border-radius: 25px;
    color: #4f46e5;
    cursor: pointer;
    transition: all 0.3s;
}

.hero-connect:hover,
.hero-resume:hover {
    background-color: #4f46e5;
    color: white;
    transform: translateY(-5px);
    box-shadow: 0px 12px 20px rgba(255, 65, 108, 0.4);
}


@media (max-width: 1024px) {
    .hero img {
        height: 250px;
        width: 250px;
    }

    .hero h1 {
        font-size: 70px;
        width: 80%;
    }

    .hero p {
        width: 70%;
        font-size: 22px;
    }

    .hero-connect,
    .hero-resume {
        padding: 18px 40px;
        font-size: 22px;
    }
}

@media (max-width: 768px) {
    .hero img {
        height: 200px;
        width: 200px;
    }

    .hero h1 {
        font-size: 50px;
        width: 90%;
    }

    .hero p {
        width: 80%;
        font-size: 20px;
    }

    .hero-action {
        flex-direction: column;
        gap: 15px;
    }

    .social-icons-left,
    .social-icons-right {
        top: auto;
        bottom: 10%;
        transform: translateY(0);
        font-size: 28px;
    }
}

@media (max-width: 480px) {
    .hero img {
        height: 150px;
        width: 150px;
    }

    .hero h1 {
        font-size: 36px;
    }

    .hero p {
        width: 90%;
        font-size: 18px;
    }

    .hero-connect,
    .hero-resume {
        padding: 15px 30px;
        font-size: 18px;
    }

    .social-icons-left,
    .social-icons-right {
        display: none;
    }
}

