:root {
    scroll-padding-top: 80px;
}


.mywork {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 30px;
    margin: 20px 100px;
}

.mywork-title {
    position: relative;
}

.mywork-title h1 {
    font-size: 600;
    color: #e63946;
    margin-bottom: 30px;
    position: relative;
    display: inline-block;
}

.mywork-title h1::after {
    content: "";
    width: 50%;
    height: 4px;
    background-color: #e63946;
    display: block;
    margin: 10px auto 0;
    border-radius: 2px;
}


.mywork-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 15px;
    width: 100%;
    justify-items: center;
}

.work-card {
    position: relative;
    width: 100%;
    max-width: 500px;
    height: 200px;
    overflow: hidden;
    border-radius: 11px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.work-card img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease-in-out;
    cursor: pointer;
}

.work-card:hover img {
    transform: scale(1.1);
    border-radius: 11px;
}

.work-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    border: 5px solid #ff0000;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
    padding: 20px;
    text-align: center;
}

.work-card:hover .work-overlay {
    opacity: 1;
}

.work-overlay h3 {
    font-size: 24px;
    margin: 0;
    font-weight: 600;
}

.work-overlay p {
    font-size: 16px;
    margin: 10px 0;
}

.work-icon {
    margin-top: 10px;
    font-size: 28px;
    color: white;
    transition: transform 0.3s;
}

.work-icon:hover {
    transform: scale(1.2);
}

.mywork-showmore {
    display: flex;
    gap: 15px;
    border-radius: 50px;
    border: 2px solid rgb(255, 0, 0);
    padding: 20px 40px;
    font-size: 22px;
    font-weight: 500;
    margin-bottom: 10px;
    transition: 0.6s;
    cursor: pointer;
}

.mywork-showmore:hover {
    background-color: rgb(255, 0, 0);
    color: white;
    transition: 0.6s;
}


@keyframes fadeInBounce {
    0% {
        opacity: 0;
        transform: translateY(-50px);
    }

    60% {
        opacity: 1;
        transform: translateY(10px);
    }

    100% {
        transform: translateY(0);
    }
}