:root {
    scroll-padding-top: 70px;
}

.services {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 30px;
    margin: 20px 100px;
}

.services-title h1 {
    font-size: 400;
    color: #e63946;
    margin-bottom: 30px;
    position: relative;
    display: inline-block;
}

.services-title h1::after {
    content: "";
    width: 50%;
    height: 4px;
    background-color: #e63946;
    display: block;
    margin: 10px auto 0;
    border-radius: 2px;
}

.services-format {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 20px;
    justify-content: center;
    border-radius: 10px;
    border: 2px solid blueviolet;
    transition: 0.4s;
    cursor: pointer;
}


.services-container {
    display: grid;
    gap: 20px;
    margin-bottom: 20px;
    grid-template-columns: 1fr 1fr 1fr;
}

.services-format h3 {
    font-size: 28px;
    font-weight: 600;
}

.services-format h2 {
    font-size: 38px;
    font-weight: 800;
    background: linear-gradient(270deg, #aeaeae 10%, #df0808 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.services-format p {
    color: #DF8908;
    font-size: 24px;
    line-height: 40px;
    max-width: 300px;
}

.read {
    display: flex;
    gap: 20px;
    align-items: center;
    margin-top: 20px;
}


.services-format:hover {
    border: 2px solid #0044ff;
    transform: scale(1.05);
    transition: 0.4s;
}

@media (max-width: 1024px) {
    .services {
        gap: 40px;
        margin: 60px 40px;
    }

    .services-title h1 {
        font-size: 50px;
    }

    .services-container {
        grid-template-columns: 1fr 1fr;
    }

    .services-format {
        padding: 30px;
    }
}

@media (max-width: 768px) {
    .services-container {
        grid-template-columns: 1fr;
    }

    .services-title h1 {
        font-size: 40px;
    }

    .services-format h2 {
        font-size: 28px;
    }

    .services-format p {
        font-size: 18px;
        line-height: 28px;
    }
}

@media (max-width: 480px) {
    .services {
        margin: 40px 10px;
        gap: 30px;
    }

    .services-title h1 {
        font-size: 32px;
    }

    .services-format {
        padding: 20px;
    }

    .services-format h2 {
        font-size: 24px;
    }

    .services-format p {
        font-size: 16px;
        line-height: 24px;
    }
}